@charset "UTF-8";
/* リキッドレイアウト対応 */
body {
  font-family: "Gotu", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.btn-border {
  position: relative;
  display: inline-block;
}
.btn-border::after {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 100%;
  height: 88%;
  border: 0.0625rem solid #4e7f85;
  z-index: 0;
}
.btn-border .btn-border__inner {
  position: relative;
  display: inline-block;
  padding: 1rem 5.5rem 1rem 2.5rem;
  background-color: #4e7f85;
  color: #fff;
  font-size: 0.875rem;
  font-family: "Lato", sans-serif;
  text-decoration: none;
  z-index: 1;
  overflow: hidden;
  transition: color 0.3s ease;
}
.btn-border .btn-border__inner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: #fff;
  z-index: -1;
  transition: width 0.6s ease-in-out;
  border: 0.0625rem solid #4e7f85;
}
.btn-border .btn-border__inner:hover::before {
  width: 100%;
}
.btn-border .btn-border__inner:hover {
  color: #4e7f85;
}
.btn-border .btn-border__inner:hover .btn-border__arrow {
  border-color: #4e7f85;
}
.btn-border .btn-border__arrow {
  position: absolute;
  top: 50%;
  right: 2.5rem;
  transform: translateY(-50%) skew(45deg);
  width: 2.5rem;
  height: 0.75rem;
  border-bottom: 0.0625rem solid #fff;
  border-right: 0.0625rem solid #fff;
  transition: border-color 0.3s ease;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: rgba(13, 41, 54, 0.68);
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 2.5rem;
}

.header__logo a {
  max-width: 8.3125rem;
  display: block;
  width: 100%;
}

.header__nav {
  display: flex;
  flex-direction: column;
}

.header__nav-list {
  display: flex;
  gap: 2.5625rem;
}

.header__nav-item a {
  position: relative;
  color: #fff;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
}
.header__nav-item a::after {
  position: absolute;
  bottom: -0.25rem;
  left: 0;
  content: "";
  width: 100%;
  height: 0.125rem;
  background: #fff;
  transform: scale(0, 1);
  transform-origin: center top;
  transition: transform 0.3s;
}
.header__nav-item a:hover::after {
  transform: scale(1, 1);
}

.header__nav-item__en {
  font-family: "Gotu", sans-serif;
  font-size: 1.125rem;
  line-height: 100%;
  font-weight: 400;
  display: block;
}

.header__nav-item__jp {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 0.625rem;
  font-weight: 400;
  line-height: 1.5;
  margin-top: 0.25rem;
  display: block;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1130px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.mv__swiper-slide {
  position: relative;
}

.swiper__img {
  width: 100%;
  display: block;
}
.swiper__img img {
  width: 100%;
  height: auto;
  display: block;
}

.mv__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
}

.mv__maintext {
  font-family: "Lato", sans-serif;
  font-size: 5rem;
  font-weight: 700;
  letter-spacing: 2.7rem;
  font-weight: 400;
}

.mv__subtext {
  color: #fff;
  font-family: "Gotu", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
}

.section-title {
  text-align: center;
}

.section-title__en {
  color: #0D2936;
  font-family: "Gotu", sans-serif;
  font-size: 2.5rem;
  font-weight: 400;
  line-height: 40px;
}
@media screen and (min-width: 768px) {
  .section-title__en {
    font-size: 2rem;
  }
}

.section-title__en--accent {
  color: #0D2936;
  font-family: "Gotu", sans-serif;
  font-size: 2.5rem;
  font-weight: 400;
  line-height: 2.5rem;
}
@media screen and (min-width: 768px) {
  .section-title__en--accent {
    font-size: 2rem;
  }
}

.section-title__jp {
  color: #4e7f85;
  text-align: center;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 150%; /* 150% */
  margin-top: 0.5rem;
}
@media screen and (min-width: 768px) {
  .section-title__jp {
    line-height: 1.125rem; /* 150% */
    margin-top: 0.25rem;
  }
}

.test {
  font-size: 6.25rem;
  background-color: #000;
  color: #4e7f85;
}

.test2 {
  font-family: "Gotu", sans-serif;
  font-weight: 500;
  font-size: 6.25rem;
  color: #0D2936;
}